<template>
  <v-container>
    <v-layout>
      <div class="split left">
        <div class="centered">
            <img class="img-responsive2" src="../assets/logo.png">
       
        <div class="container mx-auto">
                  <div class="max-w-md mx-auto my-10 p-5 rounded-md">
                       <div class="text-center">
                       <h1 class="my-3 text-3xl font-semibold text-gray-700 dark:text-gray-200">LOGIN</h1>
                     <p class="text-gray-400 dark:text-#2B4255-400">welcome to MintiCity</p>
            </div>
           
            <div class="m-7">
                    
            <v-form class="" ref="formLogin" @submit="login">
              <v-text-field
                label="Username"
                v-model="user"
                :rules="[validations.required]"
                @keyup.enter="login"
              ></v-text-field>
              <v-text-field
                label="Password"
                v-model="password"
                type="password"
                :rules="[validations.required]"
                @keyup.enter="login"
              ></v-text-field>
              
            </v-form>

            <div class="form-row ">
                  <input type="checkbox" class="" id="checkbox"  name="checkbox">
                    <span class="ml-2 text-sm text-gray-600 bar-left" for="checkbox">Remember</span>

                   <a class="underline text-sm text-gray-600 hover:text-gray-900 " >
                        Forget Password
                    </a>
            </div>
            <v-btn color="primary" @click="login">Login</v-btn>


            </div>
            </div>
    </div>
         
          
        </div>
      </div>

      
    <div class="split right">
        <div class="centered">
             <img class="img-responsive2"  src="../assets/bg.jpg">
       </div>
    </div> 

    </v-layout>
  </v-container>
</template>

<script>
  import jwt from 'jsonwebtoken'

  export default {
    data: () => ({
      user: '',
      password: '',
      validations: {
        required: v => !!v || 'This is a required field!'
      }
    }),
    methods: {
      login() {
        if (this.$refs.formLogin.validate()) {
          jwt.sign({
            user: this.user,
            password: this.password
          }, 'jwtSecret', (err, token) => {
            window.localStorage.setItem('token', token)
            this.$router.push('/')
          })
        }
      }
    }
  }
</script>

